@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
.all {
    margin: 0;
    height: 100%;
    background: black;
    color: white;
}

.menu {
    margin: 0;
    height: calc(100% - 16px);
    max-height: calc(100vw * .95);
    background: black;
    color: white;
    border: 8px solid #ff4cf7;
}

.options {
    height: calc(100% - 270px);
}

.text {
    height: 75px;
    position: relative;
    padding: 30px;
    display: -webkit-flex;
    display: flex;
}

.text:nth-child(3) {
    -webkit-align-items: flex-end;
            align-items: flex-end;
}
.keyboard {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: calc(100vh - 100vw - 150px);
    display: none;
}

@media screen and (max-width: 768px) {
    .keyboard {
        display: block;
    }
}

.keyboard > button {
    height: 20%;
    background: white;
    border: 2px solid #ff4cf7;
    color: #ff4cf7;
    font-size: xx-large;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
}

.up, .down {
    margin: auto;
    width: 50%;
}

.left, .right {
    float: left;
    width: 50%;
}

.enter,.space {
    width: 100%;
}
ol {
    width: 300px;
    margin: auto;
    list-style: none;
    counter-reset: li;
}

li::before {
    content: counter(li);
    color: white;
    display: inline-block;
    width: 1em;
    margin-left: -2em;
    margin-right: 1em;
}

li {
    padding: 10px;
    counter-increment: li;
    color: white;
}

.selected {
    background: white;
    color: black;
}
.full {
    margin: 0;
    height: 100%;
    background: #000574;
    color: white;
}

.level, .score {
    position: absolute;
    top: 20px;
    left: 20px;
    width: 20%;
}

.title, .lives {
    position: absolute;
    width: 20%;
    text-align: center;
    top: 20px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    padding: 5px;
}

.title {
    border-bottom: 3px solid white;
    border-top: 3px solid white;
}

.lives {
    top: 10px;
    height: 50px;
    width:180px;
}

.life {
    background-image: url(/number-munchers/static/media/muncher.f6020cca.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    height: 50px;
    display: inline-block;
    margin-right: 10px;
    width: 50px;
}

.score > span:nth-child(1) {
    top: 8px;
    position: relative;
}

.points {
    border: 3px solid white;
    padding: 5px;
    width: 50%;
    float: right;
}
.square {
    border: 1px solid #ff4cf7;
    float: left;
    height: 100%;
    position: relative;
    margin-right: -2px;
    margin-top: -2px;
    padding: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 16.666%;
}
.info {
    width: 100%;
    height: 75px;
    position: relative;
}

.board {
    border: 4px double #ff4cf7;
    position: relative;
    width: 95%;
    height: calc(100% - 160px);
    max-height: calc(100vw * .95);
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}

.board-row {
    height: 20%;
    width: 100%;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}
.muncher {
    position: absolute;
    background-image: url(/number-munchers/static/media/muncher.f6020cca.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    z-index: -1;
    top: 0;
    height: 20%;
    padding: 0;
    text-align: center;
    width: 16.666%;
}
.notification {
    position: absolute;
    background: #000574;
    width: calc(100% - 1px);
    height: calc(20% - 2px);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    z-index:1;
    left:1px;
    top: 40%;
}
html, body, #root {
    margin: 0;
    height: 100%;
    color: white;
    background: black;
}

body {
    margin: 0;
    font-family: 'Press Start 2P', cursive;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 24px;
    font-weight: bold;
}

@media screen and (max-width: 768px) {
    body {
        font-size: 14px;
    }
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
